<template>
	<view>
		<template v-if="searchList.length === 0">
			<!--搜索历史-->
			<view class="py-2 font-md px-2">
				搜索历史
			</view>
			<view class="flex flex-wrap">
				<view 
				hover-class="bg-light"
				@click="clickSearchHistory(item)"
				class="border rounded font mx-2 my-1 px-2" v-for="(item,index) in list" :key
				="index">
					{{item}}
				</view>
			</view>
		</template>
		<template v-else>
			<!--数据列表-->
			<block v-for="(item,index) in searchList" :key="index">
				<template v-if="type === 'post'">
					<common-list :item="item" :index="index" @follow="follow" @doSupport="doSupport"></common-list>
				</template>
				<template v-else-if="type === 'topic'">
					<topicList :item="item" :index="index"></topicList>
				</template>
				<template v-else>
					<userList :item="item" :index="index"></userList>
				</template>
				
			</block>
		</template>
		
	</view>
</template>

<script>
	const demo = [
		{
			username: '昵称',
			userpic: '../../static/default.jpg',
			newstime: '2019-10-20 下午04:30',
			isFollow: false,
			title: '我是标题',
			titlepic: '../../static/demo/datapic/11.jpg',
			support: {
				type: '',
				support_count: 0,
				unsupport_count: 0
			},
			comment_count: 2,
			share_num: 2
		},
		{
			username: '昵称',
			userpic: '../../static/default.jpg',
			newstime: '2019-10-20 下午04:30',
			isFollow: false,
			title: '我是标题',
			titlepic: '../../static/demo/datapic/11.jpg',
			support: {
				type: '',
				support_count: 0,
				unsupport_count: 0
			},
			comment_count: 2,
			share_num: 2
		},
		{
			username: '昵称',
			userpic: '../../static/default.jpg',
			newstime: '2019-10-20 下午04:30',
			isFollow: false,
			title: '我是标题',
			titlepic: '../../static/demo/datapic/11.jpg',
			support: {
				type: '',
				support_count: 0,
				unsupport_count: 0
			},
			comment_count: 2,
			share_num: 2
		}
	];
	
	const demo2 = [
		{
			cover : "/static/bgimg/1.jpg",
			title : "话题名称",
			desc : "话题描述",
			today_count : 0,
			news_count : 10
		},{
			cover : "/static/bgimg/1.jpg",
			title : "话题名称",
			desc : "话题描述",
			today_count : 0,
			news_count : 10
		},
		{
			cover : "/static/bgimg/1.jpg",
			title : "话题名称",
			desc : "话题描述",
			today_count : 0,
			news_count : 10
		},
		{
			cover : "/static/bgimg/1.jpg",
			title : "话题名称",
			desc : "话题描述",
			today_count : 0,
			news_count : 10
		},
		{
			cover : "/static/bgimg/1.jpg",
			title : "话题名称",
			desc : "话题描述",
			today_count : 0,
			news_count : 10
		}
	];
	
	const demo3 = [{
		avatar : "../../static/default.jpg",
		username : "123",
		sex : 1, // 0为知 1女性 2男性
		age : 24,
		isFollow : true
	},{
		avatar : "../../static/default.jpg",
		username : "123",
		sex : 2, // 0为知 1女性 2男性
		age : 24,
		isFollow : false
	}]

	import commonList from '@/components/common/common_list.vue';
	import topicList from '../../components/news/topic-list.vue';
	import userList from '../../components/user-list/user-list.vue';
	export default {
		components:{
			commonList,topicList,userList
		},
		data() {
			return {
				searchText : '',
				list : ['uni-app','uni-app2','uni-app3','uni-app4',],
				// 请求结果
				searchList : [],
				// 搜索类型
				type : "post",
			}
		},
		onLoad(e) {
			this.type = ''
			if(e.type){
				this.type = e.type
			}
			let pageTitle = '帖子'
			switch(this.type){
				case 'post' : pageTitle = '帖子' ; break;
				case 'topic' : pageTitle = '话题' ; break;
				case 'user' : pageTitle = '用户' ; break;
			}
			// 修改搜索占位
			// #ifdef APP-PLUS
			let currentWebview = this.$mp.page.$getAppWebview();
			let tn = currentWebview.getStyle().titleNView;
			tn.searchInput.placeholder = '搜索' + pageTitle;
			currentWebview.setStyle({
				titleNView:tn
			})
			// #endif
			
		},
		onNavigationBarButtonTap(e) {
			if(e.index === 0){
				this.searchEvent()
			}
			console.log(this.searchText)
		},
		onNavigationBarSearchInputChanged(e) {
			console.log(e.text)
			this.searchText = e.text
		},
		methods: {
			// 点击历史搜索事件
			clickSearchHistory(text){
				this.searchText = text
				this.searchEvent()
			},
			// 搜索事件
			searchEvent(){
				// 请求搜索
				uni.hideKeyboard()
				// 处于loading状态
				uni.showLoading({
					title: '加载中',
					mask:false
				})
				// 请求搜索
				setTimeout(() => {
					switch(this.type){
						case 'post' : this.searchList = demo; break;
						case 'topic' : this.searchList = demo2; break;
						case 'user' : this.searchList = demo3; break;
					}
					console.log(this.searchList)
					// 隐藏loading
					uni.hideLoading()
				},2000)
			}
		}
	}
</script>

